.output-description {
  overflow-y: auto;
  color: grey;
  border-radius: 10px;
}

.backdrop {
  position: fixed;
  top: 25%;
  bottom: 20%;
  left: 25%;
  right: 25%;
  padding: 5px;
  background-color: white;
  border: 0.1vh solid white;
  height: fit-content;
  opacity: 0.9;
  border-radius: 5px;
}

.title-popup {
  text-align: center;
  font-family: "Muli";
  color: #007bff;
  font-size: 1.1rem;
}
